import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { i18nPage, i18nId, I18n } from "../util/I18n";
import { route } from "../constants/general";

class MenuDesktop extends React.Component {

    render() {
        const language = this.props.language;

        return (
            <div className="row mt-5 p-4 menu-desktop">
                <div className="col-md-4 m-auto text-center">
                    <Link to={route.Dash}>
                        <img className="rounded-circle" src="/img/menu_ic_1_n.png" alt="Menu monitoring" width="150" />
                    </Link>
                    <h2 className="font-size-res-3"><I18n page={i18nPage.Menu} id={i18nId.MenuTitleItemDashboard} language={language} /></h2>
                    <p className="font-size-res-1"><I18n page={i18nPage.Menu} id={i18nId.MenuDescItemDashboard} language={language} /></p>
                </div>
                <div className="col-md-4 m-auto text-center">
                    <Link to={route.Hist}>
                        <img className="rounded-circle" src="/img/menu_ic_2_n.png" alt="Menu historical" width="150" />
                    </Link>
                    <h2 className="font-size-res-3"><I18n page={i18nPage.Menu} id={i18nId.MenuTitleItemHistory} language={language} /></h2>
                    <p className="font-size-res-1"><I18n page={i18nPage.Menu} id={i18nId.MenuDescItemHistory} language={language} /></p>
                </div>
                <div className="col-md-4 m-auto text-center">
                    <Link to={route.Fore}>
                        <img className="rounded-circle" src="/img/menu_ic_3_n.png" alt="Menu forecast" width="150" />
                    </Link>
                    <h2 className="font-size-res-3"><I18n page={i18nPage.Menu} id={i18nId.MenuTitleItemForecast} language={language} /></h2>
                    <p className="font-size-res-1"><I18n page={i18nPage.Menu} id={i18nId.MenuDescItemForecast} language={language} /></p>
                </div>
            </div>            
        );
    }
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { language: state.language };
};

export default connect(mapStateToProps)(MenuDesktop);