import React from "react";
import { connect } from "react-redux";
import { setLoading } from "../actions/Action";

import { i18nPage, i18nId, I18n } from "../util/I18n";
import Dimens from "../util/Dimens";

class Profile extends React.Component {
    _isMounted = false;

    constructor(props) {
		super(props);

		this.state = {
			error: '',
			attributes: [],
			dimens: new Dimens()
        };
    }

    componentDidMount() {
        let cognitoUser = this.props.cognitoUser;
        this._isMounted = true;
		this.props.setLoading(true);

		window.addEventListener("resize", () => {
			if (this._isMounted) {
				this.setState({ dimens: new Dimens() });
		  	}
		});
		
		if (cognitoUser != null) {

			cognitoUser.getSession((error, session) => {
				//debugger;
				if(error){
					console.log('An error getting the session: ' + error);
					this.props.setLoading(false);
					return;
				}
				else if(session.isValid()){
					/* // NOTE: getSession must be called to authenticate user before calling getUserAttributes */
					cognitoUser.getUserAttributes( (err, attributes) => {
						if (err) {
							// Handle error
                            console.log("An error getting attributes: " + err);
                            this.setState({ error: err });
						} else {
							// Do something with attributes
							this.setState({ attributes: attributes.reverse() });                            
                        }
                        
                        this.props.setLoading(false);
					});
				}
				else{
					console.log('Unexpected value getting the session');
					this.props.setLoading(false);
				}
			});
		}
		else{
			console.log('The cognito user is empty');
			this.props.setLoading(false);
		}
    }
    
    componentWillUnmount() {
        this._isMounted = false;
        this.props.setLoading(false);
    }
    
    validatePhoneView(){
        var mClass = null;

        if(this.state.dimens.width < 1020){
            mClass = "col-12";
        }
        else {
            mClass = "col-6";
        }


        return mClass; 
    }

    render(){
        const language = this.props.language;

        return (
            <React.Fragment>
                {
                    this.state.attributes.length > 0 ?
                    <div className="container-profile">
                        <div className="font-size-res-0">
                            {
                                this.state.attributes.map( (item) => 
                                    item.Name === "email" ?                                                          
                                    <div key={item.Name} className="parent">
                                        <div className="container-icon">
                                            <img className="icon" src="/img/ic_email.png" alt="ic-email" width="30" />
                                        </div>
                                        <div className="container-text">
                                            <span id="email">{item.Value}</span>
                                        </div>
                                    </div>
                                    :
                                    item.Name === "given_name" ?  
                                    <div key={item.Name} className="parent">
                                        <div className="container-icon">
                                            <img className="icon" src="/img/ic_name.png" alt="ic-name" width="30" />
                                        </div>
                                        <div className="container-text">
                                            <span id="given_name">{item.Value}</span>
                                        </div>
                                    </div>
                                    :
                                    item.Name === "phone_number" ?  
                                    <div key={item.Name} className="parent">
                                        <div className="container-icon">
                                            <img className="icon" src="/img/ic_phone.png" alt="ic-phone" width="30" />
                                        </div>
                                        <div className="container-text">
                                            <span id="phone_number">{item.Value}</span>
                                        </div>
                                    </div>
                                    :
                                    item.Name === "phone_number_verified" ?  
                                    <div key={item.Name} className="parent">
                                        <div className="container-icon">
                                            <img className="icon" src="/img/ic_phone_verified.png" alt="ic-phone-verified" width="30" />
                                        </div>
                                        <div className="container-text">
                                            <span id="phone_number_verified" className="">
                                                { 
                                                    item.Value ? 
                                                    <I18n page={i18nPage.Profile} id={i18nId.ProfileVerifiedYes} language={language} /> 
                                                    : 
                                                    <I18n page={i18nPage.Profile} id={i18nId.ProfileVerifiedNo} language={language} /> 
                                                }
                                            </span>
                                        </div>
                                    </div>
                                    :
                                    item.Name === "email_verified" ?  
                                    <div key={item.Name} className="parent">
                                        <div className="container-icon">
                                            <img className="icon" src="/img/ic_email_verified.png" alt="ic-email-verified" width="30" />
                                        </div>
                                        <div className="container-text">
                                            <span id="email_verified">
                                                { 
                                                    item.Value ? 
                                                    <I18n page={i18nPage.Profile} id={i18nId.ProfileVerifiedYes} language={language} /> 
                                                    : 
                                                    <I18n page={i18nPage.Profile} id={i18nId.ProfileVerifiedNo} language={language} /> 
                                                }
                                            </span>
                                        </div>
                                    </div>
                                    : null
                                )
                            }
                        </div>
                    </div>
                    :
                    null 
                }
            </React.Fragment> 
        );
    }
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { cognitoUser: state.cognitoUser, language: state.language };
};

export default connect(mapStateToProps, { setLoading })(Profile);