import { ActionType } from "../constants/general";

export function setAuth(payload) {
	return { type: ActionType.AUTH_STATUS, payload };
}

export function setCognitoUser(payload) {
	return { type: ActionType.COGNITO_USER, payload };
}

export function setJWTToken(payload) {
	return { type: ActionType.JWT_TOKEN, payload };
}

export function setLoading(payload) {
	return { type: ActionType.LOADING_STATUS, payload };
}

export function setLanguage(payload) {
	return { type: ActionType.APP_LANGUAGE, payload };
}