import React from "react";
import { Route, Switch, withRouter } from "react-router-dom";

import { connect } from "react-redux";
import { setAuth, setCognitoUser, setJWTToken, setLanguage } from "./actions/Action";

import * as AmazonCognitoIdentity from 'amazon-cognito-identity-js';

import Home from "./containers/Home";
import Login from "./containers/Login";
import Menu from "./containers/Menu";
import Dash from "./containers/Dash";
import Hist from "./containers/Hist";
import Fore from "./containers/Fore";
import Map from "./containers/Map";
import NotFound from "./containers/NotFound";

import PrivateRoute from "./components/PrivateRoute";
import PublicRoute from "./components/PublicRoute";
import Navbar from "./components/Navbar";
import { Loading } from "./components/Loading";

import { i18nLanguage } from "./util/I18n";
import { route } from "./constants/general";

import "./App.scss";

class App extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			isChecking: true,
			cognitoUser: '',
			path: [route.Menu, route.Profile, route.Dash, route.Hist, route.Fore]
		};

	}

	handleLogout = (event) => {

		event.preventDefault();

		//The redirect is in the NavLink of LogOut
		this.props.setAuth(false);
		let cognitoUser = this.props.cognitoUser;

		if (cognitoUser != null) {
			cognitoUser.signOut();
		}
	}

	componentDidMount(){

		this.props.setLanguage(i18nLanguage.Spanish); // default language

		let poolData = {
			UserPoolId: 'us-west-2_YVFxZMFQh', // your user pool id here
			ClientId: '4u6i4l80gdo9m08uhvfa4n4127' // your app client id here
		};

		let userPool = new AmazonCognitoIdentity.CognitoUserPool(poolData);

		let cognitoUser = userPool.getCurrentUser();

		if (cognitoUser != null) {
			cognitoUser.getSession((error, session) => {
				//debugger;
				if(error){
					this.props.setAuth(false);
					this.props.setCognitoUser(null);
					this.setState({ isChecking: true });
					console.log('An error getting the session: ' + error);
				}
				else if(session.isValid()){
					this.props.setJWTToken(session.getIdToken().getJwtToken());
					this.props.setCognitoUser(cognitoUser);					
					this.setState({ isChecking: false });
					this.props.setAuth(true);
					console.log('Valid session');
				}
				else{
					this.props.setAuth(false);
					this.props.setCognitoUser(null);
					this.setState({ isChecking: true });
					console.log('Unexpected value getting the session');
				}
			});
		}
		else{
			this.props.setAuth(false);
			this.props.setCognitoUser(null);
			this.setState({ isChecking: false });
			console.log('The cognito user is empty');
		}
	}

	render() {

		const vProps = {
			isAuth: this.props.isAuth
		};

		// Checking the path for drawing the navbar, not all windows requiere it.
		var sc = this.state.path.includes(this.props.location.pathname) && this.props.isAuth;
		
		return (
			!this.state.isChecking &&
			<div>
				<Navbar showNavbar={sc} pathLocation={this.props.location.pathname} isAuth={this.props.isAuth} handleLogout={this.handleLogout} />
				<Switch>
					<Route path={route.Home} exact component={Home} />
					<PublicRoute path={route.Login} component={Login} props={vProps} />
					<PrivateRoute path={route.Menu} component={Menu} props={vProps} />
					<PrivateRoute path={route.Dash} component={Dash} props={vProps} />
					<PrivateRoute path={route.Hist} component={Hist} props={vProps} />
					<PrivateRoute path={route.Fore} component={Fore} props={vProps} />
					<Route path={route.Geofence} component={Map} />
					<Route component={NotFound} />
				</Switch>
				<Loading showLoading={this.props.isLoaded} />
			</div>
		);
	}
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { isAuth: state.isAuth, cognitoUser: state.cognitoUser, isLoaded: state.isLoaded };
};

export default withRouter(connect(mapStateToProps,{setAuth, setCognitoUser, setJWTToken, setLanguage})(App));

/*
App component does not have access to the router props directly
since it is not rendered inside a Route component. To be able to
use the router props in our App component we will need to use
the withRouter Higher-Order Component (or HOC).
*/