import React from 'react';
import { connect } from "react-redux";
import { setLoading } from "../actions/Action";

import { i18nPage, i18nId, I18n } from "../util/I18n";
import Dimens from "../util/Dimens";
import { endpoint } from "../constants/general";

import { requestGET } from "../services/HttpService";

class Fore extends React.Component {
	_isMounted = false;

	constructor(props) {
		super(props);
		this.state = {
			error: "",
			isLoaded: false,
			forecasts: [],
			dimens: new Dimens()
		};
	}

	componentDidMount() {
		this._isMounted = true;
		this.props.setLoading(true);
		this.getForecasts();

		window.addEventListener("resize", () => {
			if (this._isMounted) {
				this.setState({ dimens: new Dimens() });
		  	}
		});
	}

	componentWillUnmount() {
		this._isMounted = false;
		this.props.setLoading(false);
	}

	getForecasts(){
		if (this._isMounted) {
			requestGET(endpoint.forecasts, this.props.jwtToken).then(
				result => {
					this.props.setLoading(false);
					this.setState({ isLoaded: true, forecasts: result });
				},
				// Note: it's important to handle errors here
				// instead of a catch() block so that we don't swallow
				// exceptions from actual bugs in components.
				error => {
					this.props.setLoading(false);
					this.setState({ error: error });
				}
			); 
		}
	}

	validateCenterView(){
		var mClass = null;

		if(this.state.forecasts != null) {
			if (this.state.dimens.width > 1200 && this.state.forecasts.length >= 4) {
				mClass = 'hg';
			}

			if (this.state.dimens.width > 1760 && this.state.forecasts.length >= 4) {
				mClass = null;
			}

			if (this.state.dimens.width > 1760 && this.state.forecasts.length >= 6) {
				mClass = 'hg';
			}
		}
		 
		return mClass;
	}

	render() {
		const { isLoaded, forecasts } = this.state;
		const language = this.props.language;

		return (
			<div className="container">
				<div className="m-5 p-2"/> {/*Navbar overlays the content, padding is required to force the next component to go down*/}
				<div className={`container-forecast ${this.validateCenterView()}`}>
					<div className="m-auto text-center title-fore">
						<h3 className="font-size-res-3"><I18n page={i18nPage.Forecast} id={i18nId.ForecastTitle} language={language} /></h3>
						<h5 className="font-size-res-2"><I18n page={i18nPage.Forecast} id={i18nId.ForecastSubtitle} language={language} /></h5>
					</div>
					{
						isLoaded ?
							forecasts.length > 0 ?
							<div className="parent-item-fore">
								{
									forecasts.map( (item) => 
										<div key={item.device + "-" + item.forecast} className="item-fore">
											<div className="container-title">
												<h5 className="font-size-res-1">{item.device}</h5>
												<span className="triangle"></span>
											</div>
											<div className="container-desc font-size-res-0">
												<p>
													{
														item.level === "H" ? 
														<React.Fragment>
															<span className="fore-color-red">
																<I18n page={i18nPage.Forecast} id={i18nId.ForecastHighProbability} language={language} />
															</span> 
														</React.Fragment>
														: 
														item.level === "M" ? 
														<React.Fragment>
															<span className="fore-color-green">
																<I18n page={i18nPage.Forecast} id={i18nId.ForecastLowProbability} language={language} />
															</span>
														</React.Fragment>	
														: 
														item.level === "L" ? 
														<React.Fragment>
															<span className="fore-color-skyBlue">
																<I18n page={i18nPage.Forecast} id={i18nId.ForecastNoProbability} language={language} />
															</span> 
														</React.Fragment>
														: null								
													}
													&nbsp;<I18n page={i18nPage.Forecast} id={i18nId.ForecastFrost12hours} language={language} /> <span className="fore-color-gray">{item.forecast}%</span>
												</p>						
											</div>
										</div>
									)
								}
							</div>
							:
							<div className="alert alert alert-info text-center font-size-res-0" style={{width:"100%"}} role="alert">
								<I18n page={i18nPage.Forecast} id={i18nId.ForecastAlertInfo} language={language} />
							</div>
						: null
					}					
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { jwtToken: state.jwtToken, language: state.language };
};

export default connect(mapStateToProps, { setLoading })(Fore);