import React from 'react';

import Dimens from "../util/Dimens";

let mMapCmpnt = null;

const handleClickMarkerAux = (searchTag) => {
    if(mMapCmpnt != null){
        mMapCmpnt.props.onClickMarker(searchTag);
    }
}

class Map extends React.Component {
    
    markers = [];
    isMarkersLoaded = false;
    _isMounted = false;

    constructor(props) {
        super(props);

        this.state = {
            isMapLoaded: false,
            map: '',
			dimens: new Dimens()
        };

        this.createMap = this.createMap.bind(this);
        this.handleClickMarker.bind(this);
    }

    createMap() {
        var location = {lat: 5.5425466, lng: -73.3481806};
        var zoom = 5;

        if(this.props.devices.length > 0){
            location = { lat: this.props.devices[0].lat, lng: this.props.devices[0].lng };
            zoom = 10;
        }

        this.setState({
            map: new window.google.maps.Map(
                document.getElementById('google-map'), 
                {
                    zoom: zoom, 
                    center: location, 
                    disableDefaultUI: false 
                }
            ),
            isMapLoaded: true    
        });
    }

    createMarkers() {
        if(!this.isMarkersLoaded){

            var sizeMarkerIcon = this.getSizeMarkerIcon();
            /* console.log(sizeMarkerIcon); */

            var icon = { 
                url: "img/map_dash_icon.png", // url
                scaledSize: new window.google.maps.Size(sizeMarkerIcon.width, sizeMarkerIcon.height), // size
            };

            this.props.devices.forEach((item) => {
                var location = {lat: item.lat, lng: item.lng};
                var searchTag = {device: item.device, thingName: item.city};
                
                var marker = new window.google.maps.Marker({
                    position: location,
                    map: this.state.map,
                    searchTag: searchTag,
                });

                marker.setIcon(icon);
                marker.addListener('click', this.handleClickMarker);

                this.markers.push(marker);
            });

            this.isMarkersLoaded = true;
        }        
    }

    getSizeMarkerIcon(){
        var width = 35, height = 45;

        if(this.state.dimens.width > 2550){
            width = 45;
            height = 55;
        } 
        
        if(this.state.dimens.width > 3830){
            width = 55;
            height = 65;
        }

        if(this.state.dimens.width > 7670){
            width = 65;
            height = 75;
        }

        return { width: width, height: height}
    }

    handleClickMarker() {
        var searchTag = this.searchTag;
        handleClickMarkerAux(searchTag);
    }

    createCircle = () => {
        new window.google.maps.Circle({
            strokeColor: '#FF0000',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: '#FF0000',
            fillOpacity: 0.35,
            map: this.state.map,
            center: { lat: this.props.devices[0].lat, lng: this.props.devices[0].lng },
            radius: 500
        });
    }

    componentDidMount() {
        mMapCmpnt = this;
        this._isMounted = true;
	
		window.addEventListener("resize", () => {
			if (this._isMounted) {
                this.setState({ dimens: new Dimens() });
                this.reSizeMarkerIcon();
		  	}
		});

        if (!window.google) {
            let s = document.createElement('script');
            s.type = 'text/javascript';
            s.src = `https://maps.google.com/maps/api/js?key=AIzaSyDF8QRNgcaEJWlQqDdB_7RVU19QWRe4PUg`;
            window.document.body.appendChild(s);
            // Below is important. 
            //We cannot access google.maps until it's finished loading
            s.addEventListener('load', () => {
                console.log("Map loaded");
                this.createMap();                
            })
        } else {
            console.log("Map loaded 2");
            this.createMap();
        }
    }

    componentWillUnmount() {
        /* console.log(this.markers); */
        this._isMounted = false;

        this.setMap(null);
    }

    setMap(map){
        this.markers.forEach((marker) => {
            marker.setMap(map);
        });
    }

    reSizeMarkerIcon(){
        var sizeMarkerIcon = this.getSizeMarkerIcon();
        /* console.log(sizeMarkerIcon); */

        var icon = { 
            url: "img/map_dash_icon.png", // url
            scaledSize: new window.google.maps.Size(sizeMarkerIcon.width, sizeMarkerIcon.height), // size
        };
        
        this.markers.forEach((marker) => {
            marker.setIcon(icon);
        });        
    }

    paintOnMap(){
        if(this.state.isMapLoaded) {
            this.createMarkers();
            //this.createCircle();
        }    
    }

    render() {
        return (
            <React.Fragment>
                {this.paintOnMap()}
                <div className="map" id={'google-map'} />
            </React.Fragment>
        );
    }
}

// defaultProps
Map.defaultProps = {
    devices: []
};

export default Map;