import React from "react";
import { Route, Redirect } from "react-router-dom";

import { redirect } from "../constants/general";

const PublicRoute = ({ component: C, props: cProps, ...rest }) =>
  <Route {...rest} render={props => !cProps.isAuth ? <C {...props} {...cProps} /> : <Redirect to={redirect.Public} />} />;

export default PublicRoute;

/*
If you’re not supposed to pass a function to component, what’s the solution?
Turns out the React Router team predicted this problem and gave us a handy solution.
Instead of using component, use the render prop. Render accepts a functional
component and that function won’t get unnecessarily remounted like with component.
*/