import React from "react";

const BgColorsName = {
    Black: "black",
    Blue: "blue"
}

class Loading extends React.Component {
  
  getClassBgColor() {
    let bgColor= "";
    
    switch(this.props.bgColor){
        default:
            bgColor = "bg-color-black-50";
            break;
        case BgColorsName.Black:
            bgColor = "bg-color-black-50";
            break;
        case BgColorsName.Blue:
            bgColor = "bg-color-blue-50";
    }

    return bgColor;
  }

  render() {
    return (
        this.props.showLoading ?
        <div className={`container-loading ${this.getClassBgColor()}`}>
            <div className="container-spinner">
                <div className="loader"></div>
            </div>
        </div>
        : null  
    );
  }
}

// defaultProps
Loading.defaultProps = {
  showLoading: true,
  bgColor: BgColorsName.Black
};

export { Loading, BgColorsName }