import React from 'react';

class Map extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            map: ''
        };

        this.createMap = this.createMap.bind(this);
    }

    createMap() {
        this.setState({ map: new window.google.maps.Map(document.getElementById('google-map'), { zoom: 16, center: { lat: 43.642567, lng: -79.387054 }, disableDefaultUI: false }) });
    }

    createMarker() {
        new window.google.maps.Marker({ position: { lat: 43.642567, lng: -79.387054 }, map: this.state.map });
    }

    createCircle = () => {
        new window.google.maps.Circle({
            strokeColor: '#FF0000',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: '#FF0000',
            fillOpacity: 0.35,
            map: this.state.map,
            center: { lat: 43.642567, lng: -79.387054 },
            radius: 500
        });
    }

    componentDidMount() {
        if (!window.google) {
            let s = document.createElement('script');
            s.type = 'text/javascript';
            s.src = `https://maps.google.com/maps/api/js?key=AIzaSyDF8QRNgcaEJWlQqDdB_7RVU19QWRe4PUg`;
            window.document.body.appendChild(s);
            // Below is important. 
            //We cannot access google.maps until it's finished loading
            s.addEventListener('load', () => {
                this.createMap();
                this.createCircle();
            })
        } else {
            this.createMap();
        }
    }

    render() {
        return (
            <div className="map" id={'google-map'} />
        );
    }
}

export default Map;