import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { i18nPage, i18nId, I18n } from "../util/I18n";
import { route } from "../constants/general";

class MenuMobile extends React.Component {

    render() {
        const language = this.props.language;

        return (
            <div className="container-menu-mobile">
                <div className="container-menu-items">
                    <Link to={route.Dash}>
                        <div className="menu-item">
                            <img className="rounded-circle" src="/img/menu_ic_1_n.png" alt="Menu monitoring" width="120" />
                            <div className="container-item bc-item-1">
                                <h3><I18n page={i18nPage.Menu} id={i18nId.MenuTitleItemDashboard} language={language} /></h3>
                                <p><I18n page={i18nPage.Menu} id={i18nId.MenuDescItemDashboard} language={language} /></p>
                            </div>
                        </div>
                    </Link>
                    <Link to={route.Hist}>
                        <div className="menu-item">
                            <img className="rounded-circle" src="/img/menu_ic_2_n.png" alt="Menu historical" width="120" />
                            <div className="container-item bc-item-2">
                                <h3><I18n page={i18nPage.Menu} id={i18nId.MenuTitleItemHistory} language={language} /></h3>
                                <p><I18n page={i18nPage.Menu} id={i18nId.MenuDescItemHistory} language={language} /></p>
                            </div>
                        </div>
                    </Link>
                    <Link to={route.Fore}>
                        <div className="menu-item">
                            <img className="rounded-circle" src="/img/menu_ic_3_n.png" alt="Menu forecast" width="120" />
                            <div className="container-item bc-item-3">
                                <h3><I18n page={i18nPage.Menu} id={i18nId.MenuTitleItemForecast} language={language} /></h3>
                                <p><I18n page={i18nPage.Menu} id={i18nId.MenuDescItemForecast} language={language} /></p>
                            </div>
                        </div>
                    </Link>
                </div>
            </div>            
        );
    }
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { language: state.language };
};

export default connect(mapStateToProps)(MenuMobile);