import React from "react";

export default class SeePassword extends React.Component {
    constructor(props) {
      super(props);

      this.state = {
        showPwd: false
      };
  
      this.handleClickSeeIcon = this.onClickSeeIcon.bind(this);
    }
  
    onClickSeeIcon() {
      const currentShowPwd = this.state.showPwd;
      this.setState({ showPwd: !currentShowPwd });
    }
  
    render() {
      return (
        <div className="container-see-input font-size-res-1">
          <input
            type={this.state.showPwd ? "text" : "password"}
            id={this.props.id}
            name={this.props.name}
            minLength={this.props.minLength}
            maxLength={this.props.maxLength}
            className= "see-input"
            value={this.props.value}
            onChange={this.props.onChange}
            placeholder={this.props.placeholder}
            required
          />
          <span className="container-see-icon">
            <img
              className="see-icon"
              src={
                this.state.showPwd
                  ? "img/see_ic_1.svg"
                  : "img/see_ic.svg"
              }
              alt=""
              width="30"
              onClick={this.handleClickSeeIcon}
            />
          </span>
        </div>
      );
    }
}

// defaultProps
SeePassword.defaultProps = {
  minLength: 8,
  maxLength: 30
};