import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { i18nPage, i18nId, I18n } from "../util/I18n";
import { route } from "../constants/general";

class Home extends React.Component {
    render(){
        const language = this.props.language;

        return (
            <div className="bg-home" >
                <div className="container">
                    <div className="d-flex flex-column align-items-center justify-content-center">
                    <div className="mb-auto"/>{/*Force margin to bottom*/}
                        <div className="p-1">
                            <img className="mt-3 mb-3 logo" src="/img/logo_sm_ic_1.png" width="310" alt="Logo of Faoa Home"/>
                        </div>
                        <div className="p-1 text-center text-white">
                            <h1 className="mt-2 mb-5 title-home"><I18n page={i18nPage.Home} id={i18nId.HomeTitle} language={language} /></h1>
                            <hr className="my-4"/>
                            <h4 className="mt-3 mb-5 desc-home"><I18n page={i18nPage.Home} id={i18nId.HomeDescription} language={language} /></h4>
                        </div>
                        <div className="p-1 container-btn-light">
                            <Link className="mt-5 mb-5 btn btn-light font-size-res-0" to={route.Login}><I18n page={i18nPage.Home} id={i18nId.HomeBtnToLogin} language={language} /></Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { language: state.language };
};

export default connect(mapStateToProps)(Home);