import React from 'react';
import { connect } from "react-redux";
import { setLoading } from "../actions/Action";

import Graphics from '../components/Graphics';

import { i18nPage, i18nId, I18n } from "../util/I18n";
import { endpoint } from "../constants/general";

import { requestGET } from "../services/HttpService";

class Hist extends React.Component {
	_isMounted = false;

	constructor(props) {
		super(props);

		let t = new Date(); // Instance to modify the timezone

		this.state = {
			error: null,
			isResult: false,
			isLoaded: false,
			datetime: new Date(t.getTime() - t.getTimezoneOffset() * 60000).toISOString().replace(/\.\d+Z$/g, ''),
			epoch: new Date(new Date().toISOString().replace(/\.\d+Z$/g, '')).getTime(),
			value: '',
			items: [],
			options: null
		};

		this.handleChange = this.handleChange.bind(this);
		this.handleInputChange = this.handleInputChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	componentDidMount() {
		this._isMounted = true;
		this.props.setLoading(true);
		this.getDevices();
	}

	componentWillUnmount() {
		this._isMounted = false;
		this.props.setLoading(false);
	}

	getDevices(){
		if (this._isMounted) {
			requestGET(endpoint.devices, this.props.jwtToken).then(
				result => {
					/* console.log("result", result.records); */
										
					if(result.records){
						var item0 = result.records[0];
						var options = result.records.map( (item) => <option key={item[0].stringValue} value={item[0].stringValue}>{item[1].stringValue}</option>);
						
						this.setState({ value: item0[0].stringValue, options: options });			
					}					

					this.props.setLoading(false);
				},
				// Note: it's important to handle errors here
				// instead of a catch() block so that we don't swallow
				// exceptions from actual bugs in components.
				error => {
					this.props.setLoading(false);
					this.setState({ error: error });
				}
			); 
		}
	}

	handleChange(event) {
		event.preventDefault();
		this.setState({ value: event.target.value });
	}

	handleInputChange(event) {
		event.preventDefault();
		this.setState({ [event.target.name]: event.target.value, epoch: new Date(event.target.value).getTime() });
	}

	handleSubmit(event) {
		event.preventDefault();
		this.setState({ isLoaded: true });

		fetch(endpoint.devices + this.state.value + "/records?date=" + this.state.epoch,
			{
				method: 'GET',
				headers: {
					'Authorization': this.props.jwtToken
				}
			})
			.then(res => res.json())
			.then(
				(result) => {
					this.setState({ isLoaded: false, items: result.Items });
					
					if(result){
						this.setState({ isResult: true});	
					}else{
						this.setState({ isResult: false});
					}
				},
				// Note: it's important to handle errors here
				// instead of a catch() block so that we don't swallow
				// exceptions from actual bugs in components.
				(error) => {
					this.setState({ isLoaded: false, error: error });
				}
			)
	}

	render() {
		const { error, isLoaded, items, isResult, options } = this.state;
		const language = this.props.language;

		if (error) {
			return <div>Error: {error.message}</div>;
		}
		else {

			let data = items.map((item) => {
				return [new Date(item.payloadtimestamp), item.data.T, item.data.H, Math.pow(item.data.H / 100, 1 / 8) * (110 + item.data.T) - 110];
			});
			let data1 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.P / 100
			]);
			let data2 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.L
			]);
			let data3 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.R
			]);
			let data4 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.BAT.C
			]);

			data.unshift(['Date', 'Temperature', 'Humidity', 'Td']);
			data1.unshift(['Date', 'Pressure']);
			data2.unshift(['Date', 'Light']);
			data3.unshift(['Date', 'Radiation']);
			data4.unshift(['Date', 'Battery']);
			//	console.log(data);

			return (
				<div className="container-fluid">
					<div className="m-5 p-2"/> {/*Navbar overlays the content, padding is required to force the next component to go down*/}
					<div className="row">
						<div className="col-auto col-auto-hist">
							<form className="p-1">
								<div className="row">
									<div className="form-group order-2 ml-2">
										<select className="form-control-select" value={this.state.value} onChange={this.handleChange}>
											{options}
										</select>
									</div>
									<div className="form-group order-1 ml-2">
										<input className="form-control font-size-res-0" type="datetime-local" name="datetime" value={this.state.datetime} onChange={this.handleInputChange} step="1" />
									</div>
									<div className="form-group order-2 ml-2">
										<button type="button" className="btn btn-md btn-primary btn-block font-size-res-0 btn-hist" onClick={this.handleSubmit}>
										{
											isLoaded ?
											<div>
												<span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
												<span className="sr-only">Loading ...</span>
											</div> : <I18n page={i18nPage.General} id={i18nId.GeneralBtnSend} language={language} />
										}
										</button>
									</div>
								</div>
							</form>
						</div>
					</div>
					{
						items.length > 0 ?
						<Graphics 
							temperatureData={data} 
							barometricData={data1} 
							visibleLightData={data2} 
							solarRadiationData={data3}
							batteryData={data4} />
						: 
						isResult ?
						<div className="alert alert alert-info text-center font-size-res-0" role="alert">
							<I18n page={i18nPage.General} id={i18nId.GeneralAlertInfo} language={language} />
						</div> : null
					}
				</div>
			);
		}
	}
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { jwtToken: state.jwtToken, language: state.language };
};

export default connect(mapStateToProps, { setLoading })(Hist);