const requestGET = (endpoint, accessToken = null) => {
    var uri = encodeURI(endpoint);
    var headers = getHeaders(accessToken);
  
    let response = fetch(uri, {
        method: 'GET',
        headers: headers,
    }).then(res => res.json());
  
    return response;
};
  
const requestPOST = (endpoint, data, accessToken = null) => {
    var uri = encodeURI(endpoint);
    var headers = getHeaders(accessToken);
  
    let response = fetch(uri, {
        method: 'POST',
        headers: headers,
        body: data
    }).then(res => res.json());
  
    return response;
};
  
const requestPUT = (endpoint, data, accessToken = null) => {
    var uri = encodeURI(endpoint);
    var headers = getHeaders(accessToken);
  
    let response = fetch(uri, {
        method: 'PUT',
        headers: headers,
        body: data
    }).then(res => res.json());
  
    return response;
};
  
const requestDELETE = (endpoint, accessToken = null) => {
    var uri = encodeURI(endpoint);
    var headers = getHeaders(accessToken);
  
    let response = fetch(uri, {
        method: 'DELETE',
        headers: headers
    }).then(res => res.json());
  
    return response;
};
  
const getHeaders = (accessToken) => {
    // Defining headers.
    var headers = {
        /*"Content-Type": "application/json" */
    };
  
    if (accessToken != null) {
        //console.log("accessToken", accessToken);
        Object.assign(headers, { 'Authorization': accessToken });
    }

    //console.log("headers", headers);
  
    return headers;
}
  
export { requestGET, requestPOST, requestPUT, requestDELETE };
  