import React from 'react';

import { connect } from "react-redux";
import { setJWTToken, setLoading } from "../actions/Action";

import * as AWS from "aws-sdk";
import DeviceSdk from 'aws-iot-device-sdk';

import Map from '../components/Map';
import Graphics from '../components/Graphics';

import { i18nPage, i18nId, I18n } from "../util/I18n";
import { endpoint } from "../constants/general";

import { requestGET } from "../services/HttpService";

class Dash extends React.Component {
	_isMounted = false;
	
	constructor(props) {
		super(props);

		this.state = {
			error: '',
			//jwtToken: '',
			isLoaded: false,
			isToggleOn: false,
			isResult: false,
			thing: '',
			command: '',
			device: '',
			items: [],
			devices: [],
			isMap: false,
			isGraphicsModal: false,
			thingName: ""
		};

		this.handleChange = this.handleChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleClickMarker = this.handleClickMarker.bind(this);
		this.handleClickCloseModal = this.handleClickCloseModal.bind(this);
	}

	getDevices(){
		if (this._isMounted) {
			requestGET(endpoint.devices, this.props.jwtToken).then(
				result => {
					//console.log("result", result.records);

					if(result.records){
						result.records.forEach((item) => {
							this.state.devices.push({
								device: item[0].stringValue, 
								city: item[1].stringValue, 
								lat: item[2].doubleValue, 
								lng: item[3].doubleValue	
							});
						});

						if(result.records.length === this.state.devices.length){
							this.setState({ isMap: true });
						}
					}

					this.props.setLoading(false);
				},
				// Note: it's important to handle errors here
				// instead of a catch() block so that we don't swallow
				// exceptions from actual bugs in components.
				error => {
					this.props.setLoading(false);
					this.setState({ error: error });
				}
			); 
		}
	}

	componentDidMount() {

		let cognitoUser = this.props.cognitoUser;
		this._isMounted = true;

		this.props.setLoading(true);

		if (cognitoUser != null) {

			cognitoUser.getSession((error, session) => {
				//debugger;
				if(error){
					console.log('An error getting the session: ' + error);
					this.props.setLoading(false);
					return;
				}
				else if(session.isValid()){
					//debugger;

					//Set the JWT and Call the enpoint to load the last five hours
					//this.setState({ jwtToken: session.getIdToken().getJwtToken()});
					this.props.setJWTToken(session.getIdToken().getJwtToken());

					// Initialize the Amazon Cognito credentials provider
					AWS.config.region = 'us-west-2'; // Region
					AWS.config.credentials = new AWS.CognitoIdentityCredentials({
						IdentityPoolId: 'us-west-2:b44135bd-2d70-4573-b3eb-413076376e57',
						Logins: {
							'cognito-idp.us-west-2.amazonaws.com/us-west-2_YVFxZMFQh': session.getIdToken().getJwtToken()
						}
					});

					// Make the call to obtain credentials
					AWS.config.credentials.get(() => {

						// Credentials will be available when this function is called.

						let accessKeyId = AWS.config.credentials.accessKeyId;
						let secretAccessKey = AWS.config.credentials.secretAccessKey;
						let sessionToken = AWS.config.credentials.sessionToken;
						let rndClient = 'iWeb-' + Math.floor((Math.random() * 1000) + 1);

						let IoT = new AWS.Iot({accessKeyId: accessKeyId, secretAccessKey: secretAccessKey, sessionToken: sessionToken});
						IoT.attachPrincipalPolicy({
							policyName: 'Faoa_IoT_Web_Policy',
							principal: AWS.config.credentials.identityId
						}, (err, res) => {
							if (err) console.error(err);
							
							// Connect to AWS IoT MQTT
							let device = DeviceSdk.device({
								region: 'us-west-2',
								host: 'axiyln2cq3245-ats.iot.us-west-2.amazonaws.com',
								clientId: rndClient,
								protocol: 'wss',
								accessKeyId: accessKeyId,
								secretKey: secretAccessKey, // NOTE the difference in variable names https://github.com/aws/aws-iot-device-sdk-js/issues/166
								sessionToken: sessionToken,
							});

							this.setState( {device: device} );

							device
								.on('connect', () => {
									console.log('Connected to AWS IoT');
									this.getDevices();
									//device.subscribe(this.state.value, () => {console.log('Info','Subscribed to ' + this.state.value)})
									//device.publish('/sub', JSON.stringify({ gps: 'on'}), () => {console.log('Info','Publishing to pub')});
								});
							
							device
								.on('close', () => {
									console.log('Connection closed to AWS IoT');
									this.props.setLoading(false);
								});

							device
								.on('message', (topic, payload) => {
									let temp = {deviceId: topic, payloadtimestamp: new Date().getTime(), data: JSON.parse(payload.toString())};
									let dum = [...this.state.items];
									dum.slice(1,1);
									dum.push(temp);
									this.setState( {items: dum} );
									console.log('message', temp);
							});

						});
		
					});
				}
				else{
					console.log('Unexpected value getting the session');
					this.props.setLoading(false);
				}
			});

		}
		else{
			console.log('The cognito user is empty');
			this.props.setLoading(false);
		}
	}

	componentWillUnmount() {
		
		let device = this.state.device;
		this._isMounted = false;
		this.props.setLoading(false);

		if(this.state.isToggleOn){
			device.unsubscribe(this.state.thing + '/c1', () => {console.log('Info','Unsubscribed from ' + this.state.thing + '/c1')});
		}

		if(device){
			device.end(false);
		}
	}


	handleChange(event) {

		event.preventDefault();
		this.setState({ [event.target.name]: event.target.value });
	}


	handleClick(event) {

		event.preventDefault();

		let device = this.state.device;

		if(event.target.name === 'btn_mon'){

			if(this.state.isToggleOn){
				device.unsubscribe(this.state.thing + '/c1', () => {console.log('Info','Unsubscribed from ' + this.state.thing + '/c1')}); // First unsubscribe from actual topic
			}
			else{
				this.refresh(); // Refresh the graphics with new data
				device.subscribe(this.state.thing + '/c1', () => {console.log('Info','Subscribed to ' + this.state.thing + '/c1')}) // Subscribe to the topic
			}

			this.setState((prevState) => {
				return {isToggleOn: !prevState.isToggleOn};
			});
		}
		else if(event.target.name === 'btn_cmd'){
			
			// Publish to the topic
			device.publish(this.state.thing + '/c2', this.state.command, () => {console.log('Info','Published to ' + this.state.thing + '/c2')});
		}
	}

	handleClickMarker(searchTag) {
		//console.log("searchTag dash", searchTag);
		this.setState({ thing: searchTag.device, thingName: searchTag.thingName });

		let device = this.state.device;
		
		this.refresh(); // Refresh the graphics with new data
		device.subscribe(this.state.thing + '/c1', () => {console.log('Info','Subscribed to ' + this.state.thing + '/c1')}) // Subscribe to the topic
		
		this.setState((prevState) => {
			return {
				isToggleOn: !prevState.isToggleOn,
				isGraphicsModal: !prevState.isGraphicsModal
			};
		});
	}

	handleClickCloseModal() {
		let device = this.state.device;
		device.unsubscribe(this.state.thing + '/c1', () => {console.log('Info','Unsubscribed from ' + this.state.thing + '/c1')}); // First unsubscribe from actual topic

		this.setState((prevState) => {
			return {
				isToggleOn: !prevState.isToggleOn,
				isGraphicsModal: !prevState.isGraphicsModal
			};
		});
	}

	isShowModal(){
		if(this.state.isGraphicsModal){
			return "m-modal-show"
		}
		else {
			return "m-modal-hide"
		}
	}

	refresh() {

		this.setState({ isLoaded: true });
		let thing = this.state.thing;

		requestGET(endpoint.devices + thing + "/records?date=" + new Date().getTime(), this.props.jwtToken)
		.then(
		  (result) => {
			//console.log(result);  
			this.setState( {isLoaded: false, items: result.Items} );
		  },
		  // Note: it's important to handle errors here
		  // instead of a catch() block so that we don't swallow
		  // exceptions from actual bugs in components.
		  (error) => {
			this.setState( {isLoaded: false, error: error} );
		  }
		)
	}

	render() {
		const { error, isLoaded, items, isToggleOn, isMap, devices } = this.state;
		const language = this.props.language;

		if (error) {
			return <div>Error: {error.message}</div>;
		}
		else {

			let data = items.map((item) => {
				return [new Date(item.payloadtimestamp), item.data.T, item.data.H, Math.pow(item.data.H / 100, 1 / 8) * (110 + item.data.T) - 110];
			});
			let data1 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.P / 100
			]);
			let data2 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.L
			]);
			let data3 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.R
			]);
			let data4 = items.map(item => [
				new Date(item.payloadtimestamp), item.data.BAT.C
			]);

			data.unshift(['Date', 'Temperature', 'Humidity', 'Td']);
			data1.unshift(['Date', 'Pressure']);
			data2.unshift(['Date', 'Light']);
			data3.unshift(['Date', 'Radiation']);
			data4.unshift(['Date', 'Battery']);
			
			/* console.log("items", items.length);
			console.log("data", data.length);
			console.log("data1", data1.length);
			console.log("data2", data2.length);
			console.log("data3", data3.length);
			console.log("data4", data4.length);

			console.log("items", items);
			console.log("data", JSON.stringify(data));
			console.log("data1", data1);
			console.log("data2", data2);
			console.log("data3", data3);
			console.log("data4", data4); */

			return (
				<React.Fragment>
					{
						isMap ?
						<React.Fragment>
							<Map devices={devices} onClickMarker={this.handleClickMarker}/>
							<div className="container-fluid">
								<div className="m-5 p-2"/> {/*Navbar overlays the content, padding is required to force the next component to go down*/}
								<div className={`m-modal ${this.isShowModal()}`} id="graphicsModal">
									<div className="modal-dialog m-dialog modal-xl modal-dialog-scrollable">
										<div className="modal-content">
											<div className="modal-header">
												<h2 className="modal-title font-size-res-2 modal-title-dash">{this.state.thingName}</h2>
												<button type="button" className="close modal-close" onClick={this.handleClickCloseModal}>×</button>
											</div>
											<div className="modal-body">
												{
													items.length > 0 && data.length >= items.length ?
													<React.Fragment>
														<div className="col-auto order-md-2">
															<form className="p-1">
																<div className="input-group input-group-dash">
																	<input type="text" className="form-control font-size-res-0" name="command" value={this.state.command} onChange={this.handleChange} placeholder={new I18n().getWordi18n(i18nPage.General, i18nId.GeneralPlaceholderCommand, language)} />
																	<div className="input-group-append">
																		<button disabled = {!isToggleOn} className="btn btn-primary font-size-res-0" type="button" name="btn_cmd" onClick={this.handleClick}><I18n page={i18nPage.General} id={i18nId.GeneralBtnSend} language={language} /></button>
																	</div>
																</div>
															</form>
														</div>
														<Graphics 
															temperatureData={data} 
															barometricData={data1} 
															visibleLightData={data2} 
															solarRadiationData={data3}
															batteryData={data4} />
													</React.Fragment>
													:
													isToggleOn && !isLoaded ?
													<div className="alert alert alert-info text-center font-size-res-0" role="alert">
														<I18n page={i18nPage.General} id={i18nId.GeneralAlertInfo} language={language} />
													</div> : null
												}
											</div>									
										</div>
									</div>
								</div> 			
							</div>
						</React.Fragment>
						:
						null
					}	
				</React.Fragment>
			);
		}
	}
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { cognitoUser: state.cognitoUser, jwtToken: state.jwtToken, language: state.language };
};

export default connect(mapStateToProps, { setJWTToken, setLoading })(Dash);