import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { i18nPage, i18nId, I18n } from "../util/I18n";
import { route } from "../constants/general";

class NotFound extends React.Component {
  render() {
    const language = this.props.language;

    return (
      <div className="container-fluid ">
        <div className="overlay">
          <div className="x1">
            <div className="cloud my-5"></div>
          </div>
          <div className="x2">
            <div className="cloud my-5"></div>
          </div>
          <div className="x3">
            <div className="cloud my-5"></div>
          </div>
          <div className="x4">
            <div className="cloud my-5"></div>
          </div>
          <div className="x5">
            <div className="cloud my-5"></div>
          </div>
          <div className="x1">
            <div className="cloud my-5"></div>
          </div>
        </div>
        <div className="container-notFound">
          <div className="flex-column">
            <h1 className="display-1"><I18n page={i18nPage.NotFound} id={i18nId.NotFoundTitle} language={language} /></h1>
            <p className="h1"><I18n page={i18nPage.NotFound} id={i18nId.NotFoundSubtitle} language={language} /></p>
            <p className="h2"><I18n page={i18nPage.NotFound} id={i18nId.NotFoundDescription} language={language} /></p>
            <Link className="btn btn-outline-secondary font-size-res-0" to={route.Menu}><I18n page={i18nPage.NotFound} id={i18nId.NotFoundBtnToMenu} language={language} /></Link>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { language: state.language };
};

export default connect(mapStateToProps)(NotFound);