import { ActionType } from "../constants/general";

const initialState = {
	isAuth: false,
	cognitoUser: '',
	jwtToken: '',
	isLoaded: false,
	language: "",
};

function mainReducer(state = initialState, action) {
	if (action.type === ActionType.AUTH_STATUS) {
		return Object.assign({}, state, {
			isAuth: action.payload
		});
	} else if (action.type === ActionType.COGNITO_USER) {
		return Object.assign({}, state, {
			cognitoUser: action.payload
		});
	}
	else if (action.type === ActionType.JWT_TOKEN) {
		return Object.assign({}, state, {
			jwtToken: action.payload
		});
	}
	else if (action.type === ActionType.LOADING_STATUS) {
		return Object.assign({}, state, {
			isLoaded: action.payload
		});
	}
	else if (action.type === ActionType.APP_LANGUAGE) {
		return Object.assign({}, state, {
			language: action.payload
		});
	}
	else {
		return state;
	}
};

export default mainReducer;