import React from "react";
import { connect } from "react-redux";
import { NavLink } from "react-router-dom";

import { i18nPage, i18nId, I18n } from "../util/I18n";
import { route } from "../constants/general";

import Profile from "../components/Profile";

class Navbar extends React.Component {

    constructor(props) {
		super(props);

		this.state = {
            isCollapsed: true,
            isMenu: false,
            navbarColor: "",
            navbarImage: "",
            isProfileModal: false
		};

		this.handleClick = this.handleClick.bind(this);
        this.handleProfileModal = this.handleProfileModal.bind(this);        
    }
    
    handleClick(event){
		event.preventDefault();
		this.setState((prevState) => {
			return {isCollapsed: !prevState.isCollapsed};
		});
    }

    handleProfileModal () {
        this.setState((prevState) => {
			return {
				isProfileModal: !prevState.isProfileModal
			};
		});
    }

    geti18nPageNavbar(){
        const pathLocation = this.props.pathLocation;
        var page = "";
        
        switch(pathLocation){
            default:
                page = i18nPage.Menu;
                break;
            case route.Profile:
                page = i18nPage.Navbar;
                break;
        }

        return page;
    }

    getTitleNavbarId(){
        const pathLocation = this.props.pathLocation;
        var titleId = "";
        
        switch(pathLocation){
            default:
                titleId = i18nId.MenuTitleItemDashboard;
                break;
            case route.Profile:
                titleId = i18nId.NavbarItemProfile;
                break;
            case route.Dash:
                titleId = i18nId.MenuTitleItemDashboard;
                break;
            case route.Hist:
                titleId = i18nId.MenuTitleItemHistory;
                break;
            case route.Fore:
                titleId = i18nId.MenuTitleItemForecast;
                break;
        }

        return titleId;
    }
    
    getBackgroundColorNavbar(){
        const pathLocation = this.props.pathLocation;
        var mClassName = "";
        
        switch(pathLocation){
            default:
                mClassName = "bg-navbar-menu";
                break;
            case route.Dash:
                mClassName = "bg-navbar-dashboard";
                break;
            case route.Hist:
                mClassName = "bg-navbar-history";
                break;
            case route.Fore:
                mClassName = "bg-navbar-forecast";
                break;
        }

        return mClassName;
    }

    getImageNavbar(){
        const pathLocation = this.props.pathLocation;
        var image = "";
        
        switch(pathLocation){
            default:
                image = "logo_sm_ic_3.png";
                break;
            case route.Menu:
                image = "logo_sm_ic_1.png";
                break;
            case route.Profile:
                image = "logo_sm_ic_1.png";
                break;
        }

        return image;
    }

    isShowModal(){
		if(this.state.isProfileModal){
			return "m-modal-show"
		}
		else {
			return "m-modal-hide"
		}
	}

    render() {
        // Get the state of hamburguer in responsive state.
        const isCollapsed = this.state.isCollapsed;
        const language = this.props.language;
        
        return (
            this.props.showNavbar ?  
            <React.Fragment>
                <nav className={`navbar navbar-expand-lg navbar-light bg-faoa fixed-top ${this.getBackgroundColorNavbar()}`}>
                    <div className="container-fluid">
                        <NavLink className="navbar-brand" to={route.Menu}>
                            <img className="m-1" src={`/img/${this.getImageNavbar()}`} width="160" alt="Logo of Faoa Nav"/>
                        </NavLink>
                        { 
                            this.props.pathLocation === route.Menu ?
                            <React.Fragment>
                                <button className={"navbar-toggler" + (isCollapsed ? " collapsed" : "")} type="button" onClick={this.handleClick} data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false">
                                    <span className="my-1 mx-2 close">X</span>
                                    <span className="navbar-toggler-icon"/>
                                </button>
                                <div className={"navbar-collapse" + (isCollapsed ? " collapse" : "")} id="navbarResponsive">
                                    <ul className="navbar-nav ml-auto mt-3">
                                        <li className="nav-item text-right">
                                            {
                                                this.props.isAuth ? 
                                                <div className="container-nav-link">
                                                    <NavLink className="nav-link" to={route.Menu} onClick={this.handleProfileModal}>
                                                        <div className="container-nav-item">
                                                            <div className="icon">
                                                                <img id="ic-profile" src="/img/ic_profile.png" alt="ic_profile" width="20" />
                                                            </div>
                                                            <div className="text">
                                                                <p className="font-size-res-0"><I18n page={i18nPage.Navbar} id={i18nId.NavbarItemProfile} language={language} /></p>
                                                            </div>
                                                        </div>
                                                    </NavLink>
                                                    <NavLink className="nav-link" to={route.Menu} onClick={this.props.handleLogout}>
                                                        <div className="container-nav-item">
                                                            <div className="icon">
                                                                <img id="ic-logout" src="/img/ic_logout.png" alt="ic_logout" width="30" />
                                                            </div>
                                                            <div className="text">
                                                                <p className="font-size-res-0"><I18n page={i18nPage.Navbar} id={i18nId.NavbarItemLogout} language={language} /></p>
                                                            </div>
                                                        </div>
                                                    </NavLink> 
                                                </div>
                                                : <NavLink className="nav-link" to={route.Login}><I18n page={i18nPage.Navbar} id={i18nId.NavbarItemLogIn} language={language} /></NavLink>
                                            }
                                        </li>
                                    </ul>
                                </div>
                            </React.Fragment>
                            :
                            <React.Fragment>
                                <div className="container-navbar-title">
                                    <div className="container-line-title">
                                        <div className="line"></div>
                                        <div className="navbar-font-color-white font-size-res-1">
                                            <I18n page={this.geti18nPageNavbar()} id={this.getTitleNavbarId()} language={language} />
                                        </div>
                                    </div>
                                </div>
                            </React.Fragment>
                        }
                    </div>
                </nav> 
                <div className={`m-modal ${this.isShowModal()}`} id="profileModal">
                    <div className="container-dialog">
                        <div className="modal-dialog modal-dialog-scrollable">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h2 className="modal-title font-size-res-2 modal-title-dash">
                                        <I18n page={i18nPage.Profile} id={i18nId.ProfileTitle} language={language} />
                                    </h2>
                                    <button type="button" className="close modal-close" onClick={this.handleProfileModal}>×</button>
                                </div>
                                <div className="modal-body">
                                    <Profile />
                                </div>									
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
            : null
        );
    }
}

// defaultProps
Navbar.defaultProps = {
    showNavbar: true,
};

const mapStateToProps = (state /*, ownProps*/) => {
	return { language: state.language };
};

export default connect(mapStateToProps)(Navbar);