import React from 'react';
import { Link } from "react-router-dom";

import { connect } from "react-redux";
import { setAuth, setCognitoUser, setJWTToken } from "../actions/Action";

import * as AmazonCognitoIdentity from 'amazon-cognito-identity-js';
import SeePassword from "../components/SeePassword";
import Alert from "../components/Alert";

import Dimens from "../util/Dimens";
import { i18nPage, i18nId, I18n } from "../util/I18n";
import { route } from "../constants/general";

class Login extends React.Component {

	_isMounted = false;

	constructor(props) {
		super(props);

		this.state = {
			email: '',
			password: '',
			newpass: '',
			givenname: '',
			isLoaded: false,
			isError: false,
			isNewPass: false,
			cognitoUser: '',
			dimens: new Dimens()
		};

		this.handleInputChange = this.handleInputChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleClick = this.handleClick.bind(this);

	}

	componentDidMount() {
		this._isMounted = true;
	
		window.addEventListener("resize", () => {
			if (this._isMounted) {
				this.setState({ dimens: new Dimens() });
		  	}
		});
	}
	
	componentWillUnmount() {
		this._isMounted = false;
	}

	handleInputChange(event) {

		event.preventDefault();
		this.setState({ [event.target.name]: event.target.value });
	}

	handleSubmit(event) {

		event.preventDefault();

		this.setState({ isLoaded: true }); // Starting the loader

		let authenticationData = {
			Username: this.state.email, // your username here
			Password: this.state.password, // your password here
		};

		let authenticationDetails = new AmazonCognitoIdentity.AuthenticationDetails(authenticationData);

		let poolData = {
			UserPoolId: 'us-west-2_YVFxZMFQh', // your user pool id here
			ClientId: '4u6i4l80gdo9m08uhvfa4n4127' // your app client id here
		};

		let userPool = new AmazonCognitoIdentity.CognitoUserPool(poolData);

		let userData = {
			Username: this.state.email, // your username here
			Pool: userPool
		};

		let cognitoUser = new AmazonCognitoIdentity.CognitoUser(userData);

		//Because of the callback in cognitoUser, a Promise must be done.
		let promise = new Promise((resolve, reject) => cognitoUser.authenticateUser(authenticationDetails, {
			onSuccess: function (result) {
				//debugger;
				//var accessToken = result.getAccessToken().getJwtToken();
				resolve(result);
			},
			onFailure: function (err) {
				//debugger;
				reject(err);
			},
			mfaRequired: function (codeDeliveryDetails) {
				let verificationCode = prompt('Please input verification code', '');
				cognitoUser.sendMFACode(verificationCode, this);
			},
			newPasswordRequired: function (userAttributes, requiredAttributes) {
				// User was signed up by an admin and must provide new 
				// password and required attributes, if any, to complete 
				// authentication.

				// userAttributes: object, which is the user's current profile. It will list all attributes that are associated with the user. 
				// Required attributes according to schema, which don’t have any values yet, will have blank values.
				// requiredAttributes: list of attributes that must be set by the user along with new password to complete the sign-in.


				// Get these details and call 
				// newPassword: password that user has given
				// attributesData: object with key as attribute name and value that the user has given.

				//debugger;

				let err = { code: 'newpassword', name: 'newpassword', message: 'password challenge' };
				reject(err);
			}
		}));

		// resolve runs the first function in .then
		promise.then(
			(result) => {
				this.setState({ isLoaded: false }); // Finishing the loader
				this.setState({ isError: false });
				this.props.setJWTToken(result.getIdToken().getJwtToken());
				this.props.setCognitoUser(cognitoUser);				
				this.props.setAuth(true);
				this.props.history.push(route.Menu);
			},
			(error) => {
				if (error.code === 'newpassword') {
					this.props.setCognitoUser(cognitoUser);
					this.setState({ isNewPass: true, cognitoUser: cognitoUser });
				}
				else {
					this.setState({ isError: true });
					this.props.setAuth(false);
					this.props.history.push(route.Login);
				}
				this.setState({ isLoaded: false }); // Finishing the loader
			}
		);

	}

	handleClick(event) {

		event.preventDefault();
		
		let attributesData = { given_name: this.state.givenname };
		let promise = new Promise((resolve, reject) => this.state.cognitoUser.completeNewPasswordChallenge(this.state.newpass, attributesData, {
			onSuccess: function (result) {
				resolve(result)
			},
			onFailure: function (err) {
				reject(err)
			}
		}));

		// resolve runs the first function in .then
		promise.then(
			(result) => {
				this.setState({ isError: false });
				this.props.setJWTToken(result.getIdToken().getJwtToken());
				this.props.setAuth(true);
				this.props.history.push(route.Menu);
			},
			(error) => {
				this.setState({ isError: true });
				this.props.setAuth(false);
				this.props.history.push(route.Login);
				console.log('Error',error);
			}
		);

		this.setState({ isNewPass: false });

	}

	render() {
		const { isError, dimens } = this.state;
		const language = this.props.language;

		return (
			<div className="bg-login">
				<div className="container">
					<div className={`row align-items-center justify-content-center ${isError && dimens.height < 550 && dimens.isPortrait() ? 'hg' : 'hg-vh'}`}>
						<form className="col-auto" onSubmit={this.handleSubmit}>
							<div className="mb-4 text-center" >
								<Link to={route.Home}>
									<img className="mt-3 mb-3 logo" src="/img/logo_sm_ic_1.png" width="300" alt="Logo of Faoa Login"/>
								</Link>
								<h1 className="h3 mb-3 font-weight-normal font-size-res-3"><I18n page={i18nPage.Login} id={i18nId.LoginTitle} language={language} /></h1>
							</div>
							<div className="form-group">
								<input type="email" className="form-control font-size-res-1" name="email" id="emailHelp" value={this.state.email} onChange={this.handleInputChange} placeholder={new I18n().getWordi18n(i18nPage.Login, i18nId.LoginPlaceholderEmail, language)} required />
								<small id="emailHelp" className="form-text text-muted font-size-res-0"><I18n page={i18nPage.Login} id={i18nId.LoginDescEmail} language={language} /></small>
							</div>
							<div className="form-group">
								<SeePassword name="password" id="passwordLog" value={this.state.password} onChange={this.handleInputChange} placeholder={new I18n().getWordi18n(i18nPage.Login, i18nId.LoginPlaceholderPwd, language)} />
							</div>
							<div className="form-group">
								<button type="submit" className="btn btn-lg btn-success btn-block font-size-res-btn" disabled = {this.state.isLoaded ? true : false}>
								{
									this.state.isLoaded ?
									<div>
										<span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
										<span className="sr-only"><I18n page={i18nPage.Login} id={i18nId.LoginBtnSubmit2} language={language} /></span>
									</div>
									: <I18n page={i18nPage.Login} id={i18nId.LoginBtnSubmit1} language={language} />
								}
								</button> 
							</div>
							<p className="mt-5 mb-3 text-dark text-center font-size-res-1">© 2019</p>
							<Alert showAlert={this.state.isError} alertMessage={new I18n().getWordi18n(i18nPage.Login, i18nId.LoginAlertMessage, language)} />
						</form>
					</div>
					{
						this.state.isNewPass ?
						<div className="c-modal" tabIndex="-1" role="dialog" aria-labelledby="modalTitle" aria-hidden="true">
							<div className="modal-dialog modal-dialog-centered modal-dialog-res" role="document">
								<div className="modal-content modal-content-res">
									<div className="modal-header">
										<h5 className="modal-title m-auto text-center font-size-res-2" id="modalTitle"><I18n page={i18nPage.Login} id={i18nId.LoginTitleModalNewpass} language={language} /></h5>
									</div>
									<div className="modal-body">
										<input type="text" className="form-control font-size-res-1" name="givenname" value={this.state.givenname} onChange={this.handleInputChange} placeholder={new I18n().getWordi18n(i18nPage.Login, i18nId.LoginPlaceholderName, language)} />
										<small className="form-text text-muted font-size-res-0"><I18n page={i18nPage.Login} id={i18nId.LoginDescName} language={language} /></small>
									</div>
									<div className="modal-body">
										<SeePassword name="newpass" value={this.state.newpass} onChange={this.handleInputChange} placeholder={new I18n().getWordi18n(i18nPage.Login, i18nId.LoginPlaceholderPwd, language)} />
										<small className="form-text text-muted font-size-res-0"><I18n page={i18nPage.Login} id={i18nId.LoginDescPwd} language={language} /></small>
									</div>
									<div className="modal-footer">
										<button className="btn btn-dark font-size-res-btn" type="button" onClick={this.handleClick}><I18n page={i18nPage.Login} id={i18nId.LoginBtnModalNewpass} language={language} /></button>
									</div>
								</div>
							</div>
						</div> : null
					}
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state /*, ownProps*/) => {
	return { language: state.language };
};

export default connect(mapStateToProps,{setAuth,setCognitoUser,setJWTToken})(Login);